  width: 100%;
  background-size: 100% 100%;
  position: relative;
}
@media only screen and (max-width: 1100px) {
  .homepage-content {
    background: #e6f5ff;
    width: 100%;
    background-size: 100% 100%;
  }
}
@media only screen and (max-width: 1100px) {
  .homepage-content .lowerhalf {
    background: url("../images/background/main/main-bg-mob-lowerhalf.png");
    width: 100%;
    background-size: 100% 100%;
  }
}
.homepage-content .content-wrapper {
  position: relative;
}
.homepage-content .content-wrapper .bg-decoration {
  position: absolute;
}
.homepage-content .content-wrapper .bg-decoration.bg-item1 {
  left: 0;
  top: 5%;
  width: calc(335px / 2);
}
.homepage-content .content-wrapper .bg-decoration.bg-item2 {
  right: 0;
  top: 10%;
  width: calc(343px / 2);
}
.homepage-content .content-wrapper .bg-decoration.bg-item3 {
  left: 0;
  top: 25%;
  width: calc(300px / 2);
}
.homepage-content .content-wrapper .bg-decoration.bg-item4 {
  right: 0;
  top: 62%;
  width: calc(250px / 2);
}
.homepage-content .content-wrapper .bg-decoration.bg-item5 {
  left: 50px;
  top: 69%;
  width: calc(150px / 2);
}
.homepage-content .content-wrapper .bg-decoration.bg-item6 {
  right: 0;
  top: 76%;
  width: calc(228px / 2);
}
.homepage-content .content-wrapper .bg-decoration.bg-item7 {
  left: 0;
  bottom: 10%;
  width: calc(250px / 2);
}
.homepage-content .content-wrapper .bg-decoration.bg-item-mob-1 {
  width: 107px;
  top: -80px;
  left: 0;
  z-index: 0;
}
.homepage-content .content-wrapper .bg-decoration.bg-item-mob-2 {
  width: 90px;
  bottom: -50px;
  right: 0;
  z-index: 3;
}
.homepage-content .content-wrapper .bg-decoration.bg-item-mob-3 {
  width: 60px;
  right: 0;
  top: 37%;
}
.homepage-content .content-wrapper .bg-decoration.bg-item-mob-4 {
  width: 97px;
  left: 0;
  top: 37%;
}
.homepage-content .content-wrapper .bg-decoration.bg-item-mob-5 {
  width: 100%;
  left: 0;
  bottom: 0;
}
.homepage-content .content-wrapper .bg-decoration.bg-item-mob-5 img {
  width: 100%;
}
.homepage-content .content-wrapper .content {
  width: 75%;
  margin: 0 auto;
}
@media only screen and (max-width: 1100px) {
  .homepage-content .content-wrapper .content {
    width: 100%;
  }
}
.homepage-content .center-content-wrapper,
.homepage-content .slider-wrapper,
.homepage-content .ticket-section-wrapper,
.homepage-content .terms-wrapper {
  position: relative;
}
@media only screen and (max-width: 1100px) {
  .homepage-content .center-content-wrapper,
  .homepage-content .slider-wrapper,
  .homepage-content .ticket-section-wrapper,
  .homepage-content .terms-wrapper {
    width: calc(100% - 40px);
    padding: 0 20px;
  }
}
.homepage-content .main-text-wrapper {
  padding-top: 50px;
}
@media only screen and (max-width: 1100px) {
  .homepage-content .main-text-wrapper {
    padding-top: 15px;
    width: 95%;
    margin: 0 auto;
  }
}
.homepage-content .center-content-wrapper {
  margin-top: 70px;
}
@media only screen and (max-width: 1100px) {
  .homepage-content .center-content-wrapper {
    margin-top: 58px;
  }
}
.homepage-content .slider-wrapper {
  margin-top: 60px;
}
@media only screen and (max-width: 1100px) {
  .homepage-content .slider-wrapper {
    margin-top: 50px;
    padding-bottom: 50px;
  }
}
.homepage-content .ticket-section-wrapper {
  padding-top: 200px;
}
@media only screen and (max-width: 1100px) {
  .homepage-content .ticket-section-wrapper {
    padding-top: 135px;
  }
}
.homepage-content .terms-wrapper {
  margin-top: 55px;
  padding-bottom: 50px;
}
@media only screen and (min-width: 1100px) {
  .homepage-content .terms-wrapper {
    padding-bottom: 50px !important;
  }
}
@media only screen and (max-width: 1100px) {
  .homepage-content .terms-wrapper {
    margin-top: 44px;
    padding-bottom: 130px;
  }
}
@media only screen and (min-width: 768px) {
  .homepage-content .terms-wrapper {
    padding-bottom: 30%;
  }
}
footer {
  background: #c9eafe;
}
@media only screen and (max-width: 1100px) {
  footer {
    padding-left: 20px;
    padding-right: 20px;
  }
}
footer h2 {
  padding-top: 82px;
}
@media only screen and (max-width: 1100px) {
  footer h2 {
    font-size: 24px;
    padding-top: 40px;
    line-height: 32px;
  }
}
footer .app-link-wrapper {
  width: 40%;
  max-width: 412px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 100px;
}
@media only screen and (max-width: 1100px) {
  footer .app-link-wrapper {
    width: 100%;
    padding-bottom: 60px;
  }
}
footer .app-link-wrapper a {
  max-width: 200px;
}
@media only screen and (max-width: 1100px) {
  footer .app-link-wrapper a {
    width: 48%;
  }
}
.main-text-wrapper {
  width: 75%;
  margin: 0 auto;
}
@media only screen and (max-width: 1100px) {
  .main-text-wrapper {
    width: 100%;
  }
}
.center-content-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 1100px) {
  .center-content-wrapper p {
    font-size: 16px;
  }
}
.banner-bg-white {
  background: #fff;
  padding: 22px 37px 31px;
  border-radius: 30px;
  display: inline-block;
  margin: 0 auto;
  z-index: 2;
}
@media only screen and (max-width: 1100px) {
  .banner-bg-white {
    padding: 20px 24px;
  }
}
.slider-wrapper .maecard-cashback-slider {
  margin-top: 25px;
}
.slider-wrapper .maecard-cashback-slider .maecard-cashback-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.slider-wrapper .maecard-cashback-slider .maecard-cashback-slide .slide-visual img {
  width: 400px;
}
@media only screen and (max-width: 1100px) {
  .slider-wrapper .maecard-cashback-slider .maecard-cashback-slide .slide-visual img {
    width: 250px;
  }
}
.slider-wrapper .maecard-cashback-slider .maecard-cashback-slide .slide-desc {
  width: 50%;
  margin-top: 20px;
}
@media only screen and (max-width: 1100px) {
  .slider-wrapper .maecard-cashback-slider .maecard-cashback-slide .slide-desc {
    width: 100%;
  }
}
.ticket-section-wrapper h1 {
  margin-bottom: 40px;
}
.ticket-section-wrapper .ticket-wrapper {
  margin-top: 5px;
}
.terms-wrapper {
  max-width: 700px;
  margin: 0 auto;
}
.terms-wrapper .term-li {
  display: flex;
}
.terms-wrapper .term-li p.numbering {
  padding-right: 10px;
}
@media only screen and (max-width: 1100px) {
  .terms-wrapper p {
    font-size: 15px;
    line-height: 20px;
  }
}
.terms-wrapper .link {
  margin-top: 40px;
}
@media only screen and (max-width: 1100px) {
  .terms-wrapper .link {
    margin-top: 20px;
  }
}
* {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  display: block;
}
img:not([src]),
visibility hidden,
img.js_lazy:not(.loaded) {
  width: 100%;
  height: 0;
  padding-bottom: 66.67%;
  position: relative;
}
a.link {
  text-decoration: underline;
  color: #222;
}
.center {
  text-align: center;
}
.upper {
  text-transform: uppercase;
}
.mob-show {
  display: none;
}
@media only screen and (max-width: 1100px) {
  .mob-show {
    display: block;
  }
}
@media only screen and (max-width: 1100px) {
  .mob-hide {
    display: none;
  }
}
.content-wrapper {
  width: auto;
  max-width: 1200px;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
}
.content-wrapper:after {
  content: "";
  display: block;
  clear: both;
}
@media only screen and (max-width: 1100px) {
  .content-wrapper {
    padding: 0;
  }
}
img {
  max-width: 100%;
}
p,
h1,
h2,
h3,
h4,
h5,
h6,
a {
  font-family: "lato";
  margin: 0;
}
h1 {
  font-size: 36px;
  font-weight: 900;
  line-height: 50px;
}
@media only screen and (max-width: 1100px) {
  h1 {
    font-size: 30px;
    line-height: 38px;
  }
}
h2 {
  font-size: 30px;
  font-weight: 900;
  line-height: 38px;
}
p {
  font-size: 20px;
  line-height: 28px;
}
@media only screen and (max-width: 1100px) {
  p.terms {
    font-size: 15px;
  }
}
p span.bold {
  font-weight: 700;
}
